.listPageContainer span.viewMore,
span.viewMore {
  display: inline;
  padding: 0;
  color: #F28F17 !important;
  text-decoration: underline;
  text-align: end;
}

h2.topRow {
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;
  color: #585858;
}


span.line {
  display: table-cell;
  width: 100%;
  vertical-align: top;
}

.filtersRow {
  margin-top: 4px;
  margin-bottom: 60px;
}

.filtersRow > :global(.col-7) {
  flex: 0 0 60%;
  max-width: 60%;
}

.filtersRow > :global(.col-5) {
  flex: 0 0 40%;
  max-width: 40%;
}


.filtersRow > .leftFilters {
  display: flex;
  justify-content: flex-end;
  text-align: end;
}

.filtersRow > .selectComponent {
  margin-left: 26px;
  top: -7px;
}

.filtersRow .listPageContainer leftFilters > .selectComponent {
  top: 0;
}

.filtersRow > .leftFilters > .selectComponent {
  min-width: 196px;
  margin-left: 25px;
  display: inline-block;

}


.leftFilters > :global(a.nav-link) {
  display: inline-block;
  padding: 0;
  min-width: 74px;
}


.filtersRow > .selectComponent,
.filtersRow > .selectComponent > form {
  min-width: 196px;
}


@media (max-width: 767px) {
  .filtersRow {
    margin-bottom: 0;
  }
  .filtersRow > .selectComponent {
    margin-left: 0;
  }
}

@media (max-width: 630px) {

  .filtersRow {
    margin-bottom: 24px;
  }
  .filtersRow > :global(.col-7) {
    display: block;
    flex: 0 0 100%;
    max-width: 100%;
  }

  .filtersRow > :global(.col-5) {
    display: block;
    flex: 0 0 100%;
    max-width: 100%;

  }

  .leftFilters > :global(a.nav-link) {
    position: absolute;
    left: 0;
    top: -55px;
  }
  .filtersRow .selectComponent,
  .filtersRow .selectComponent > form,
  .filtersRow .selectComponent > form .SearchSelector__control {
    max-width: 100%;
    width: 100%;
    margin-left: 0 !important;
  }
  .filtersRow > div,
  .filtersRow > .leftFilters > span {
    flex: initial;
    width: 100% !important;
    max-width: 100% !important;
  }

}

@media (max-width: 380px) {

  .leftFilters > :global(a.nav-link) {
    top: -94px;
  }

}

