.quickInfoCard {
  width: 252px;
  height: 156px;
  text-align: center;
  box-shadow: none;
  display: inline-block;
  margin-right: auto;
  margin-left: auto;
}

.quickInfoCard .label {
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #323232;
}

.quickInfoCard .value {
  font-weight: bold;
  font-size: 56px;
  color: #05BEC0;
}
