.typeStateTab {
  height: 28px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  background-color: rgba(5, 190, 192, 0.08) !important;

  color: #323232 !important;
  cursor: pointer;
  font-size: 14px;
  font-weight: normal;
  line-height: 16px;
  cursor: pointer;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */

  padding: 5px 16px;
  box-shadow: none;
  margin-left: 8px;
}

:global(.nav-link.active) .typeStateTab,
.typeStateTab.active {
  background-color: #05BEC0 !important;
  color: #ffffff !important;
}


@media (max-width: 991.9px) {
  .typeStateTab {
    margin-bottom: 25px;
  }
}
